* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#app {
    max-width: 400px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
}

.div_load {
    min-height: 300px;
    min-width: 300;
    margin: 50px;
}

#container {
    max-height: 500px;
    width: 500px;
    overflow-y: scroll;
    word-break: break-all;
    border: 1px solid #DCDFE6;
}